.logo{
    color: white;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}
body{
    padding: 0;
    margin: 0;
}
.navbar{
    background-color: rgb(127, 142, 255);
    display: flex;
    justify-content: space-between;
    gap: 15%;
    align-items: center;
    height: 60px;
    box-sizing: border-box;
    padding: 0px 20px
}
.searchbutton{
    border-radius: 0px 30px 30px 0px;
    background-color: #9be31c;
    border: none;
    height: 100%;
    width: 50px;
    transition: none;
}
.searchicon{
    width: 10px;
    height: auto;
}
.searchbar{
    font-size: 16px;
    font-weight: 600;
    border-radius: 30px 0px 0px 30px;
    border: none;
    height:100%;
    width: 250px;
    padding: 0 5px;
    padding-left: 20px;
}
.nav-items{
    display: flex;
    justify-content: space-between;
    gap: 5%;
    width: 30%;
    height: 30px;
}

.nav-items button{
    
    background-color: transparent;
    border: none;
    border-bottom: 3px solid transparent;
    box-shadow: none;
    color: white;
    text-align: center;
    width: auto;
    font-size:16px;
    font-weight: 700;
    transition: all .3s linear;
    

}
.nav-items button:hover{
    border-bottom: 3px solid #9be31c;

}
#search{
    display: flex;
    height: 30px;
}
.product-section{
    display: flex;
    margin: 30px;
    justify-content: space-around;
}
.productcard{
    box-shadow: 0px 5px 10px grey;
    width: 20%;
    height: auto;
    border-radius: 10px;
    padding: 0%;

}
.productcard img{
    width: 100%;
    height: 50%;
    border-radius: 10px;
}
.productdiscription{
    padding: 20px;
    border-top: 10px solid rgb(127, 142, 255);
}
.banner{
    background-image: url('https://img.freepik.com/premium-vector/pharmacy-with-pharmacist-client-counter_36082-518.jpg?w=2000');
    height: 700px;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 50px;
   

}
.product{
    background-color: rgb(127, 142, 255);
    color: white;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 30px;
    box-sizing: border-box;
    height: 100px;
}
.productdiscription button{
    background-color: rgb(127, 142, 255);
    border: none;
    height: 50px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    letter-spacing: 1px;
    border-radius: 15px;

}